@import "../mixins";
@import "_header";
@import "_footer";
.sidebar{
  background-color: #1a1d23;
  height: 100vh;
  position: sticky;
  top: 0;
  overflow-y: auto;
  &__top{
    padding: 2em 1.25em;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    border-bottom: 1px solid #7c7c7c;
  }
  &__logo{
    width: calc(100% - 1em);
    padding-right: 1em;
    img{
      display: block;
      width: 6.5625em;
    }
  }
  &__logout{
    width: 1em;
    flex-shrink: 0;
    img{
      width: 100%;
    }
  }
  &__note{
    width: 1em;
    position: relative;
    &-num{
      position: absolute;
      bottom: 0;
      left: 0;
      transform: translate(-50%,50%);
      width: 1em;
      height: 1em;
      font-size: .625em;
      background-color: #67f0bb;
      border-radius: 50%;
      color: #fff;
    }
  }
  &__note,
  &__calendar{
    display: block;
    margin: 1.875em .9375em 0 0;
    flex-shrink: 0;
    img{
      width: 100%;
    }
  }
  &__calendar{
    width: 1.1875em;
  }

}

.burger{
  display: none;
}

.nav{
  @include grid100gap(.625em);
  padding: 1em 1.375em;
  &__show,
  &__link{
    display: flex;
    align-items: center;
    color: #ffffff;
    height: 2.1875em;
    padding: 0 .75em;
    border-radius: 1.5em;
    transition: all .3s;
    &:hover{
      color: #ffffff;
      background: #495162;
      cursor: pointer;
    }
    span{
      font-size: .875em;
    }
  }
  &__icon{
    flex-shrink: 0;
    display: block;
    width: 1.1875em;
    @include center;
    margin-right: .5em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__arrow{
    margin-left: auto;
    flex-shrink: 0;
    width: .3125em;
    transition: inherit;
  }
  &__show{
    &._active{
      .nav__arrow{
        transform: rotate(45deg);
      }
    }
  }
  &__hidden{
    padding: 1.5em 0 0 1.875em;
    display: none;
  }
  &__sublinks{
    @include grid100gap(2em);
  }
  &__sublink{
    display: flex;
    align-items: center;
    color: #fff;
    &-icon{
      width: .5em;
      height: .5em;
      border-radius: 50%;
      flex-shrink: 0;
      border: .0625em solid #fff;
      margin-right: .625em;
    }
    &-text{
      font-size: .8125em;
      line-height: 120%;
    }
    &._active{
      color: $primary;
      .nav__sublink-icon{
        border-color: $primary;
      }
    }
  }
}

.wrap{
  padding: 1em 1.75em 2.875em;
}


@media screen and (max-width: 992px){
  .sidebar{
    height: auto;
    z-index: 20;
    &__top{
      justify-content: flex-end;
      padding: 1em 1.5em;
      border: none;
    }
    &__logo{
      width: auto;
      margin-right: auto;
    }
    &__note,
    &__calendar{
      margin: 0 0 0 1.5em;
    }
    &._open{
      .nav{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        background-image: url(../img/burger2.svg);
      }
    }
  }

  .burger{
    background-image: url(../img/burger.svg);
    width: 1.125em;
    height: 1.125em;
    @include bgImgSize(100%);
    flex-shrink: 0;
    display: flex;
    margin-left: 1.5em;
  }

  .nav{
    padding: 1em .75em 2em;
    position: fixed;
    overflow-y: auto;
    left: 0;
    bottom: 0;
    width: 100%;
    height: calc(100 * var(--vh) - 3.875em);
    background-color: inherit;
    z-index: 10;
    display: flex;
    flex-direction: column;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
  }

  .wrap{
    padding-left: 1.5em;
    padding-right: 1.5em;
    overflow-x: auto;
    &__scroll{
      width: 92em;
    }
  }
}
