@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_table.scss";
.tabs{
  display: flex;
  align-items: center;
}

.tab{
  padding: 0 .875em;
  height: 3em;
  border-radius: .75em .75em 0 0;
  display: flex;
  align-items: center;
  color: $dark;
  border: 1px solid transparent;
  border-bottom: none;
  transition: all .3s;
  img{
    width: .6875em;
    flex-shrink: 0;
    margin-right: .25em;
  }
  span{
    font-size: .75em;
  }
  &._active{
    border-color: #cdcae2;
    pointer-events: none;
  }
  &:hover{
    cursor: pointer;
    color: $primary;
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}
