@import "../mixins";
.referal{

}

.referal-data{
  margin-top: 2.75em;
  @include grid100gap(1.3125em)
}

.referal-group{
  background-color: #dce8f8;
  border-radius: .375em;
  &__top{
    box-shadow: 0 .1875em .3125em 0 rgba(0,0,0,0.11);
    min-height: 5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: .625em 2em;
    border-bottom: 1px solid $black;
  }
  &__info{
    max-width: 68.75em;
    @include grid100gap(.625em);
  }
  &__search{
    width: 20em;
    flex-shrink: 0;
    margin-left: 2em;
    input{
      border-color: $black;
      font-size: .875em;
      height: 3.14em;
      padding-left: 4.07em;
      background-image: url(../img/search.webp);
      background-repeat: no-repeat;
      background-position: left 1.71em center;
      background-size: 1.36em auto;
      &::placeholder{
        color: $black;
      }
    }
  }
  &__content{
    padding: 0 2em .875em;
  }
}

.referal-table{
  table{
    width: 100%;
    border-spacing: 0;
  }
  th,
  td{
    font-weight: 400;
    padding: 0 .75em;
    text-align: center;
    span{
      font-size: .875em;
    }
    &:first-child{
      text-align: left;
    }
  }
  th{
    height: 3.625em;
  }
  td{
    height: 3.25em;
  }
  tbody{
    tr{
      background-image: linear-gradient(-90.0deg, #aa67fe -24%, #16ef97 125%);
    }
  }
  &__empty{
    padding: 1.25em;
    text-align: center;
  }
  &__bottom{
    padding: 1.125em 2em;
    border-top: 1px solid $black;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .m-table__show{
      .fg{
        margin: 0 .75em 0 0;
        select{
          background-color: $primary;
          color: #fff;
        }
      }
    }
  }
  &__nav{
    display: flex;
    align-items: center;
    .pagination__btn{
      background-color: $primary;
      margin-left: .5em;
    }
  }
}


.referal-group2{
  .referal-group__content{
    padding: 0;
  }
  thead{
    tr{
      background-color: #b9ccd3;
    }
  }
  th,
  td{
    &:first-child{
      padding-left: 2.75em;
    }
    &:last-child{
      padding-right: 2.75em;
    }
  }
}
