.banking{

}

.filter{
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin-top: -1em;
  &__btn,
  &__group{
    flex-shrink: 0;
    margin-top: .75em;
    &:not(:last-child){
      margin-right: .6875em;
    }
  }
  &__group{
    background-color: #b9b3f5;
    display: flex;
    align-items: center;
    border-radius: 1.5em;
    .filter__btn{
      margin: 0;
      &:not(:last-child){
        margin-right: 1px;
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
      }
      &:not(:first-child){
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
      }
    }
  }
  .date-btn{
    margin-top: .75em;
  }
}

.date-btn{
  display: flex;
  align-items: center;
  color: $secondary;
  position: relative;
  input{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    cursor: pointer;
    z-index: 2;
    opacity: 0;
  }
  span{
    font-size: .75em;
  }
  img{
    flex-shrink: 0;
    width: .75em;
    display: block;
    margin-right: .625em;
  }
  &:hover{
    color: $primaryHover;
  }
}

.banking-fields{
  margin-top: 1.3125em;
  display: grid;
  grid-template-columns: repeat(4,calc(25% - 1.125em));
  gap: .75em 1.5em;
  &__buttons{
    display: flex;
    margin-top: 1.3125em;
  }
  &__btn{
    margin-right: .75em;
    flex-shrink: 0;
    padding: 0 1.25em;
  }
}

.banking-nums{
  display: grid;
  grid-template-columns: repeat(6,calc(100% / 6 - .625em));
  gap: .9375em .75em;
  margin-top: 2.1875em;
}

.stat-card{
  box-shadow: $shadow;
  background-color: #fff;
  padding: 1em 1.25em;
  border-radius: .75em;
  min-height: 4.8125em;
  &__num,
  &__title{
    text-align: right;
    font-size: .8125em;
    line-height: 120%;
  }
  &__img{
    display: flex;
    align-items: center;
    height: 3.75em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
}

.transactions{
  margin-top: 2.1875em;
  &__tabs{
    display: flex;
    align-items: flex-end;
    height: 3em;
  }
  &__tab{
    background-color: $secondary;
    border: 1px solid $secondary;
    border-bottom: none;
    color: #ffffff;
    height: 2.3125em;
    display: flex;
    align-items: center;
    padding: 0 1.5em;
    margin-right: .25em;
    flex-shrink: 0;
    border-radius: .625em .625em 0 0;
    transition: all .3s;
    span{
      font-size: .75em;
    }
    &._active,
    &:hover{
      border-color: #cdcae2;
      background-color: #f3f2f7;
      color: #5e5873;
      cursor: pointer;
    }
    &._active{
      height: 100%;
      pointer-events: none;
    }
  }
  &__table{
    th,td{
      &:last-child{
        width: 11.375em;
      }
    }
    th{
      &:last-child{
        border-top-right-radius: .75em;
      }
    }
  }
  &__btn{
    width: 10.75em;
    height: 1.75em;
    span{
      font-size: .75em;
    }
  }
}

.status{
  display: flex;
  align-items: center;
  &__color{
    width: .8125em;
    height: .8125em;
    flex-shrink: 0;
    border-radius: 50%;
    margin-right: .625em;
  }
}
