@import "../mixins";
.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    &__bg{
        background: #666570;
        opacity: 0.7;
        position: fixed;
        width: 100%;
        height: 100vh;
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }
    &--active{
        opacity: 1;
        pointer-events: unset;
        .modal__bg{
            display: block;
        }
    }
    &__dialog{
        border-radius: .75em;
        width: 30em;
        max-width: calc(100% - 1.25em);
        overflow-y: auto;
        overflow-x: hidden;
        max-height: 100vh;
        position: relative;
        z-index: 3;
        background: #fff;
        box-shadow: $shadow;
    }
    &__top{
        padding: 1em 1em 1em 1.25em;
        display: flex;
        align-items: center;
        justify-content: space-between;
        background-color: #e8e8e8;
    }
    &__title{
        color: #6e6b7b;
    }
    &__close{
        width: .875em;
        flex-shrink: 0;
        margin-left: 1em;
        &:hover{
            path{
                fill: $primary;
            }
        }
        svg{
            width: 100%;
            height: auto;
        }
    }
    &__nav{
        display: flex;
        align-items: center;
        justify-content: flex-end;
        margin-top: 3.375em;
    }
    &__btn{
        width: 8.75em;
        margin-left: .5em;
    }
}

.task-modal{
    &__time{
        padding: .625em 1.25em;
        display: flex;
        justify-content: space-between;
        border-bottom: $border;
        .fg__multi{
            display: flex;
        }
    }
    &__date{
        width: 7.625em;
        position: relative;
        margin-right: .5em;
        &-img{
            pointer-events: none;
            position: absolute;
            top: 1px;
            right: 1px;
            width: 4em;
            height: calc(100% - 2px);
            font-size: .625em;
            border-radius: 0 1.85em 1.85em 0;
            background-color: #f1f1f1;
            @include center;
            img{
                width: 1.2em;
            }
        }
    }
    &__hours{
        width: 4em;
        input{
            text-align: center;
        }
    }
    &__form{
        padding: 1em 1em 1.25em 1.25em;
        &-grid{
            display: grid;
            grid-template-columns: repeat(2,calc(50% - .4375em));
            gap: 1.25em .875em;
            margin-top: 1.5em;
            ._full{
                grid-column: 1/-1;
            }
        }
    }
    &__title2{
        font-size: .6875em;
        line-height: 120%;
        color: #b1b1b1;
    }

}

.client-modal{
    .modal__dialog{
        width: 45.625em;
    }
    &__nav{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 1.25em;
        border-bottom: $border;
        .tabs{
            padding-top: .875em;
        }
    }
    &__add{
        img{
            width: .75em;
            margin-right: .5em;
        }
    }
    &__content{
        padding: 2em 1.25em 1.25em;
        min-height: 32em;
        display: flex;
        flex-direction: column;
    }
    .modal__nav{
        margin-top: auto;
        padding-top: 2em;
        grid-column: 1/-1;
    }
    ._call{
        margin-right: auto;
        img{
            width: .6875em;
        }
    }
}

.channel-modal{
    .modal__dialog{
        width: 25em;
    }
    .modal__close{
        position: absolute;
        top: .5em;
        right: .5em;
    }
    &__wrap{
        padding: 2.25em 2.125em 1.75em;
    }
    &__title{
        text-align: center;
        font-size: 1.1875em;
        line-height: 1.2;
        font-weight: 400;
        color: #6e6b7b;
    }
    &__form{
        @include grid100gap(.625em);
    }
    .modal__nav{
        margin-top: .375em;
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: .625em;
    }
    .modal__btn{
        width: 100%;
        margin: 0;
    }
}


@media screen and (max-width: 992px) {
    .modal {
        &__dialog {
            max-width: 100%;
            width: 100%;
        }
        &__close{
            border-radius: 1em;
            svg{
                width: 1em;
            }
        }
    }
}
