@import "../mixins";
.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    font-size: .6875em;
    line-height: 120%;
    margin-bottom: .5em;
    color: $dark;
  }
  textarea,
  input,
  select{
    border: 1px solid #c6c2de;
    padding: 0 1.61em;
    height: 2.7em;
    width: 100%;
    font-size: .8125em;
    background-color: #fff;
    color: $black;
    border-radius: 1.85em;
    &::placeholder {
      color: $secondary;
    }
    &:focus{
      border-color: $primary;
    }
  }
  select{
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    &::-ms-expand {
      display: none; // IE
    }
    background-image: url(../img/select_arrow.webp);
    background-size: .625em auto;
    background-position: right 1.61em center;
    background-repeat: no-repeat;
    padding-right: 2.33em;
  }
  &._small{
    label{
      font-size: .625em;
    }
    select,
    input{
      font-size: .625em;
      line-height: 2.8em;
      padding: 0 1.5em;
    }
    select{
      padding-right: 2.8em;
      background-size: 1em auto;
      background-position: right 1.4em center;
    }
  }
  textarea{
    border-radius: .75em;
    padding-top: 1em;
    padding-bottom: 1em;
    resize: vertical;
    min-height: 9em;
  }
}

.m-check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        background-image: url(../img/check.svg);
        background-color: $primary;
        border-color: $primary;
      }
    }
  }
  label {
    display: flex;
    align-items: center;
    cursor: pointer;
    font-size: 1em;
    &:before {
      content: '';
      border-radius: .125em;
      background-color: #fff;
      border: $border;
      width: .75em;
      height: .75em;
      transition: all .100s;
      background-size: .5em auto;
      background-position: center;
      background-repeat: no-repeat;
    }
  }
}
