@import "../mixins";
.dashboard-data{
  margin-top: 1.875em;
  &__top{
    padding: 1.25em 1.5em;
  }
  .m-table__content{
    border-radius: .75em;
  }
}

.changes{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .5em));
  gap: 1em;
  margin-top: 1.125em;
}

.changes-card{
  border-radius: 1.25em;
  padding: 1.25em 1em 1.25em 1.75em;
  box-shadow: $shadow;
  background-color: #fff;
  &__items{
    margin-top: 1.875em;
    @include grid100gap(.625em);
    max-height: 8.375em;
    overflow-y: auto;
  }
}

.changes-item{
  padding: .125em 1em;
  position: relative;
  @include grid100gap(.25em);
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: .25em;
    height: 100%;
    background-color: #ff0000;
    border-radius: .25em;
  }
  &__desc{
    font-size: .625em;
    line-height: 150%;
    color: #1a1a1a;
  }
}
