.lendings{
  &__buttons{
    display: flex;
  }
  &__btn{
    img{
      width: 1em;
    }
  }
}

.lendings-data{
  margin-top: 1.875em;
  th,td{
    &:last-child{
      width: 11.875em;
      .th{
        justify-content: center;
      }
    }
  }
}

.chain-link{
  display: flex;
  align-items: center;
  img{
    width: 1em;
    margin-right: .5em;
    flex-shrink: 0;
  }
}
