@import "../mixins";
.m-table{
  color: $dark;
  overflow-x: auto;
  table{
    width: 100%;
    min-width: 102em;
    border-spacing: 0;
  }
  th,
  td{
    font-size: 1em;
    border-top: $border;
    &:first-child{
      padding-left: 2.125em;
    }
    &:last-child{
      padding-right: .625em;
    }
  }
  th{
    background-color: #f3f2f7;
    height: 2.5em;
    &:first-child{
      border-left: $border;
    }
    &:last-child{
      border-right: $border;
    }
    &._dark{
      background-color: #787295;
      color: #fff;
      .th__sort{
        &:before,
        &:after{
          background-image: url(../img/select_arrow2.png);
        }
      }
    }
  }
  td{
    height: 3em;
    border-top: $border;
  }
  &__content{
    border-radius: 0 0 .75em .75em;
    background-color: #fff;
  }
  &__bottom{
    padding: .75em .75em 1.5em 2.125em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-top: $border;
  }
  &__show{
    display: flex;
    align-items: center;
    .fg{
      flex-shrink: 0;
      margin-left: .75em;
      select{
        background-color: #f3f2f7;
        border-color: #f3f2f7;
        padding-left: 1.33em;
        background-position: right .83em center;
      }
    }
  }
}

.th,
.td{
  display: flex;
  align-items: center;
  &__title{
    font-size: .75em;
    line-height: 120%;
  }
  &__check{
    flex-shrink: 0;
    margin-right: .625em;
    label{
      &:before{
        margin: 0;
      }
    }
  }
}

.th{
  &__title{
    font-weight: 700;
    text-transform: uppercase;
  }
  &__sort{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: .5em;
    margin-left: .25em;
    flex-shrink: 0;
    &:before,
    &:after{
      content: '';
      display: block;
      flex-shrink: 0;
      width: 100%;
      height: .3em;
      @include bgImgSize(100%);
      margin: .0625em 0;
      background-image: url(../img/select_arrow.webp);
    }
    &:before{
      transform: rotate(-180deg);
    }
  }
}

.td{
  &__title{
    a{
      color: $primary;
      text-decoration: underline;
      &:hover{
        color: $primaryHover;
      }
    }
  }
  &__remove{
    flex-shrink: 0;
    width: .625em;
    margin-left: .625em;
    &:hover{
      img{
        transform: scale(1.1);
      }
    }
    img{
      width: 100%;
      transition: all .3s;
    }
  }
  &__btn{
    height: 1.75em;
    padding: 0 1em;
    &:not(:last-child){
      margin-right: .25em;
    }
  }
}

.pagination{
  display: flex;
  align-items: center;
  &__nums{
    background-color: #f3f2f7;
    display: flex;
    border-radius: .875em;
    margin: 0 .5em;
    flex-shrink: 0;
  }
  &__num,
  &__btn{
    @include center;
    width: 1.75em;
    height: 1.75em;
    flex-shrink: 0;
    border-radius: .875em;
    background-color: #f3f2f7;
    color: $dark;
    img{
      width: .3125em;
    }
    &:hover{
      background-color: $primary;
      color: #fff;
    }
  }
  &__num{
    span{
      font-size: .75em;
    }
    &._active{
      background-color: $primary;
      color: #fff;
    }
    &:not(:last-child){
      margin-right: .125em;
    }
    &:last-child{
      margin-left: 2em;
    }
  }
}


@media screen and (max-width: 992px){
  .m-table{
    table{
      min-width: auto;
    }
  }
}
