.company{
  &__buttons{
    margin-top: 2.375em;
  }
}

.company-data{
  margin-top: 1.75em;
  &__top{
    display: flex;
    justify-content: flex-end;
    padding: .75em;
    .m-btn{
      margin-left: .625em;
      img{
        width: 1em;
      }
    }
  }
  .m-table__content{
    border-radius: .75em;
  }
  th,td{
    &:last-child{
      width: 11.875em;
      .th{
        justify-content: center;
      }
    }
  }
}
