.clients-data{
  margin-top: 1.125em;
  padding-bottom: 5em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1.25em 1.25em 1.25em 2em;
  }
  .m-table{
    &__content{
      border-radius: .75em;
      padding-bottom: 2em;
    }
    tbody{
      tr{
        &:nth-child(3n-2){
          background-color: #e8e8e8;
        }
        &:nth-child(3n-1){
          background-color: #e5f3ff;
        }
        &:nth-child(3n){
          background-color: #ffffff;
        }
      }
    }
    th,
    td{
      padding-left: .5em;
      &:first-child{
        padding-left: .75em;
      }
      &:nth-child(7){
        padding-right: 1.5em;
      }
      &:nth-child(8){
        .fg{
          width: 7.625em;
          min-width: 100%;
        }
      }
      &:nth-child(10){
        img{
          width: .875em;
        }
      }
      &:nth-child(12){
        .fg{
          width: 4em;
          min-width: 100%;
          select{
            border: none;
            background-color: #337ab7;
            background-image: url(../img/select_arrow2.webp);
            color: #fff;
          }
        }
      }
    }
    th{
      &:nth-child(4){
        padding: 0 1em;
      }
    }
    td{
      &:nth-child(4),
      &:nth-child(10){
        padding: 0;
      }
    }
    .fg{
      select{
        font-size: .625em;
        height: 2.8em;
        padding-left: 1em;
        padding-right: 2em;
        background-size: .8em auto;
        background-position: right 1em center;
      }
    }
  }
  .th{
    &__title{
      font-size: .6875em;
    }
  }
  .td{
    &__title{
      font-size: .625em;
    }
  }
}

.call-data{
  padding: 0 1em;
  height: 1.75em;
  &:not(:last-child){
    margin-right: .25em;
  }
  img{
    width: .6875em;
    margin-right: .25em;
  }
  span{
    font-size: .625em;
  }
}

.country{
  display: flex;
  align-items: center;
  &__flag{
    width: 1.375em;
    flex-shrink: 0;
    margin-right: .25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    font-size: .625em;
  }
}

.action{
  position: fixed;
  bottom: 0;
  right: 0;
  width: calc(100% - 14.5em);
  padding: 1.25em 1.75em;
  background: rgba(#000,.7);
  color: #fff;
  display: flex;
  align-items: center;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  &._active{
    opacity: 1;
    pointer-events: unset;
  }
  &__num{
    margin-left: .625em;
    flex-shrink: 0;
    min-width: 3.75em;
    padding: 0 1em;
    height: 1.75em;
    border-radius: 1.25em;
    background-color: #fff;
    color: $dark;
    @include center;
  }
  &__select{
    margin-left: .625em;
    select{
      height: 2.33em;
      padding-left: 1.33em;
      padding-right: 2.3em;
      background-size: .67em auto;
      background-position: right 1.33em center;
    }
  }
  &__btn{
    margin-left: .625em;
    height: 1.75em;
    padding: 0 .875em;
  }
}

.client-info{
  &__top{
    display: flex;
    align-items: center;
  }
  &__item{
    display: flex;
    align-items: center;
    color: #b1b1b1;
    &:not(:last-child){
      &:after{
        content: '|';
        flex-shrink: 0;
        margin: 0 .5em;
      }
    }
    span{
      font-size: .75em;
    }
    img{
      width: .6875em;
      display: block;
      flex-shrink: 0;
      margin-right: .3125em;
    }
  }
  &__form{
    margin-top: 2.25em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: .625em 1.25em;
  }
}

.client-log{
  &__items{
    @include grid100gap(.625em);
  }
}

.log-card{
  display: grid;
  grid-template-columns: 7.875em 4.75em auto;
  &__item{
    display: flex;
    align-items: center;
  }
  &__icon{
    width: .6875em;
    flex-shrink: 0;
    margin-right: .3125em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__text{
    font-size: .6875em;
    color: $dark;
    line-height: 120%;
  }
}


@media screen and (max-width: 992px){
  .action{
    width: 100%;
  }
}
