@import 'vars';
html {
    width: 100%;
    font-size: unquote("min(100vw, 1920px)");
}

.wrapper {
    min-height: 100vh;
    position: relative;
    z-index: 2;
    display: grid;
    grid-template-columns: 14.5em calc(100% - 14.5em);
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}

.page {
    width: calc(100vw - (100vw - 100%));
    overflow-x: hidden;
    font-family: $font1;
    font-weight: 400;
    color: $black;
    font-size: .833335%;
    background: #f8f8f8;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.container {
    max-width: 1350px;
    width: 100%;
    margin: 0 auto;
    padding: 0 1.25em 0 4.375em;
}

a {
    &:hover {
        color: $primary;
    }
}


@media screen and (max-width: 992px){
    .page{
        font-size: 14px;
    }

    .wrapper{
        grid-template-columns: 100%;
    }

    .container{
        padding: 0 1em;
    }
}


