@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    font-size: 1em;
    border-radius: 1.5em;
    padding: 0 1.25em;
    height: 2.1875em;
    span{
        font-size: .75em;
    }
    &:focus {
        box-shadow: none;
    }
    img{
        width: 1.3125em;
        margin-right: .3125em;
        flex-shrink: 0;
        display: block;
    }
}


.m-btn-primary{
    color: #fff;
    background-color: $primary;
    &:hover{
        color: #fff;
        background-color: $primaryHover;
    }
}

.m-btn-primary-outline{
    color: $primary;
    border: 1px solid $primary;
    &:hover{
        color: $primaryHover;
        border-color: $primaryHover;
    }
}

.m-btn-red-outline{
    color: #de5a5a;
    border: 1px solid #de5a5a;
    &:hover{
        color: darken(#de5a5a,10%);
        border-color: darken(#de5a5a,10%);
    }
}

.m-btn-red{
    background-color: #de5a5a;
    color: #fff;
    &:hover{
        background-color: darken(#de5a5a,10%);
        color: #fff;
    }
}

.m-btn-yellow{
    background-color: #f0ad4e;
    color: #fff;
    &:hover{
        background-color: darken(#f0ad4e,10%);
        color: #fff;
    }
}

.m-btn-green{
    background-color: #5cb85c;
    color: #fff;
    &:hover{
        background-color: darken(#5cb85c,10%);
        color: #fff;
    }
}

.m-btn-blue{
    background-color: #5bc0de;
    color: #fff;
    &:hover{
        background-color: darken(#5bc0de,10%);
        color: #fff;
    }
}

.m-btn-white{
    background-color: #fff;
    color: $dark;
    border: $border;
    &:hover{
        background-color: lighten($dark,50%);
        color: $dark;
    }
}
