@import "../vars.scss";
.breadcrumbs {
    padding: 1.375em 1.75em;
    border-bottom: 1px solid #cbc8e1;
    background: #f8f8f8;
    position: sticky;
    top: 0;
    z-index: 2;
    &__item {
        font-size: .875em;
        line-height: 110%;
        position: relative;
        font-weight: 400;
        display: inline;
        &:last-child {
            color: $primaryHover;
            &:before{
                color: $black;
            }
        }
        &:not(:first-child) {
            &:before {
                content: ' / ';
                padding: 0 .25em;
            }
        }
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        position: static;
        padding: 1em 1.5em;
    }
}
